.reservation__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 131px;
  max-width: 1366px;
  margin: 0 auto;
  min-height: 555px;
}
@media (max-width: 1223px) {
  .reservation__wrapper {
    padding: 20px 40px;
  }
}
@media (max-width: 1023px) {
  .reservation__wrapper {
    flex-direction: column;
    align-items: center;
    padding: 78px 0 100px;
  }
}
@media (max-width: 767px) {
  .reservation__wrapper {
    padding-top: 50px;
    padding-bottom: 68px;
  }
}

.reservation__desc {
  max-width: 495px;
}

.reservation__desc h3 {
  font-weight: 700;
  font-size: 40px;
  line-height: 150%;
  color: #011c40;
  margin-bottom: 7px;
  margin-top: 10px;
}
@media (max-width: 1023px) {
  .reservation__desc h3 {
    font-size: 34px;
    line-height: 150%;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .reservation__desc h3 {
    font-size: 24px;
    line-height: 130%;
    margin-bottom: 12px;
  }
}

.reservation__desc p {
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: #011c40;
  margin-top: 0;
}
@media (max-width: 1023px) {
  .reservation__desc p {
    text-align: center;
    font-size: 18px;
    line-height: 150%;
    max-width: 390px;
    display: block;
    margin: 0 auto;
    margin-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .reservation__desc p {
    font-size: 16px;
    line-height: 140%;
    max-width: 275px;
    margin-bottom: 33px;
  }
}

.reservation__form form {
  padding: 44px 50px;
  background-color: #011c40;
  box-shadow: 0 4px 45px #011c40;
  max-width: 403px;
  min-height: 455px;
  margin-left: 30px;
  margin-top: 0;
}
@media (max-width: 1023px) {
  .reservation__form form {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .reservation__form form {
    padding: 36px 30px;
  }
}

.reservation__form form p {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #f9fbfd;
  margin-bottom: 27px;
  margin-top: 0;
}
@media (max-width: 767px) {
  .reservation__form form p {
    font-size: 18px;
    line-height: 140%;
    text-align: center;
  }
}

.reservation__form input {
  background-color: #f9fbfd;
  padding: 12px 16px;
  margin-bottom: 20px;
  width: 100%;
  border: none;
  outline: none;
}
.reservation__form input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: rgba(1, 28, 64, 0.7);
}

.reservation__submit label {
  cursor: pointer;
  font-weight: 400;
  font-size: 10px;
  line-height: 130%;
  color: #f9fbfd;
  margin-left: 23px;
  position: relative;
}
.reservation__submit label::before {
  position: absolute;
  top: -5px;
  left: -23px;
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .reservation__submit label::before {
    top: 3px;
    left: -26px;
  }
}
.reservation__submit label::after {
  position: absolute;
  top: -3px;
  left: -18px;
  display: block;
  width: 8px;
  height: 10px;
  border-top: 2px solid #011c40;
  border-left: 2px solid #011c40;
  transform: rotate(-135deg);
}
@media (max-width: 767px) {
  .reservation__submit label::after {
    top: 5px;
    left: -20px;
  }
}
@media (max-width: 767px) {
  .reservation__submit label {
    font-size: 10px;
    line-height: 120%;
    margin-left: 26px;
    display: block;
  }
}

.reservation__submit button {
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #011c40;
  width: 100%;
  text-align: center;
  padding: 11px;
  margin-top: 23px;
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .reservation__submit button {
    margin-top: 27px;
    padding: 10px;
  }
}

.reservation__submit input:focus-visible ~ label::before {
  border: 2px solid #000000;
}
.reservation__submit input:checked ~ label::after {
  content: " ";
}

.input-message {
  color: #f9fbfd;
}

.location__wrapper {
  display: flex;
  align-items: center;
  gap: 100px;
}
@media (max-width: 1223px) {
  .location__wrapper {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .location__wrapper {
    flex-direction: column-reverse;
    gap: 15px;
  }
}

.location {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  padding: 120px 71px;
}
@media (max-width: 1223px) {
  .location {
    padding: 100px 20px;
  }
}
@media (max-width: 1023px) {
  .location {
    padding: 100px 20px 130px;
  }
}
@media (max-width: 767px) {
  .location {
    padding: 58px 0 76px;
  }
}

.location__desc h4 {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #011c40;
  margin-bottom: 33px;
  margin-top: 18px;
}
@media (max-width: 1023px) {
  .location__desc h4 {
    margin-top: 47px;
  }
}
@media (max-width: 767px) {
  .location__desc h4 {
    text-align: start;
    margin-top: 0;
  }
}

.location__desc ul {
  padding-left: 28px;
}
@media (max-width: 1023px) {
  .location__desc ul {
    margin-top: -5px;
  }
}

.location__desc li {
  list-style: none;
  position: relative;
  margin-bottom: 20px;
}

.location__desc a {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #011c40;
  transition: color, 0.5s;
}
.location__desc a:hover {
  color: #808080;
}

.location__desc svg {
  position: absolute;
  top: 2px;
  left: -28px;
}

.map {
  background-color: #808080;
  position: relative;
}

@media (max-width: 1023px) {
  .map iframe {
    width: 324px;
    height: 335px;
  }
}
@media (max-width: 767px) {
  .map iframe {
    width: 290px;
    height: 316px;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
  }
}

