.article {
  position: absolute;
  top: 234px;
  left: 43%;
  transform: translate(-50%);

  @include vp-1023 {
    left: 56%;
    width: 100%;
  }

  @include vp-767 {
    top: 152px;
    left: 55%;
    z-index: 0;
  }
}

.article h1 {
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: $color-white;
  text-shadow: 0 4px 4px #00000040;
  margin-bottom: 12px;

  @include vp-1023 {
    font-size: 50px;
    line-height: 61px;
  }

  @include vp-767 {
    font-size: 36px;
    line-height: 125%;
  }
}

.article p {
  margin-top: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: $color-white;
  text-shadow: 0 4px 4px #00000040;

  @include vp-1023 {
    font-size: 18px;
    line-height: 22px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 136%;
  }
}
